import React, { Fragment } from 'react';
import Router from 'next/router';
import pageLayout from '../mocks/test.json';
import ComponentRegistry from '../components/componentRegistry';

export default class Index extends React.Component {
  renderComponents = (compArr, nested) => {
    return compArr.map(item => {
      if (typeof item === 'string') {
        const Component = ComponentRegistry[item];
        return (
          <Fragment key={Math.random()}>
            {!nested && <h3>Direct -{item}</h3>}
            Component Name: {item}
            <Component />
            <br />
          </Fragment>
        );
      } else {
        return (
          <Fragment key={Math.random()}>
            <h4>Nested -- {item.join(' // ')}</h4>
            {this.renderComponents(item, true)}
          </Fragment>
        );
      }
    });
  };

  render() {
    const compArr = pageLayout.layout;
    return <div>{this.renderComponents(compArr)}</div>;
  }
}
